import React, { useContext } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUsersCog } from "@fortawesome/free-solid-svg-icons";
import Navigation from "../Navigation/Navigation";
import { AppContext } from "../../store/store";
import Leaders from "./Leaders/Leaders";

const HomePage = () => {
  const {
    restOfUsers,
    firstThree,
    firstThreeByGames,
    restOfUsersByGames,
  } = useContext(AppContext);

  return (
    <>
      <Link to="/our-game-ranking/user" className="HomePage__userCogWrapper">
        <FontAwesomeIcon className="HomePage__userCog" icon={faUsersCog} />
      </Link>
      <Router>
        <div className="HomePage">
          <h2 className="HomePage__header">Leaderboard</h2>
          <Navigation />

          <Switch>
            <Route exact path="/our-game-ranking">
              <Leaders data={firstThree} restOfData={restOfUsers} />
            </Route>

            <Route exact path="/our-game-ranking/perGame">
              <Leaders
                data={firstThreeByGames}
                restOfData={restOfUsersByGames}
              />
            </Route>

            <Route exact path="/our-game-ranking/percentage">
              <Leaders
                data={firstThreeByGames}
                restOfData={restOfUsersByGames}
              />
            </Route>
          </Switch>
        </div>
      </Router>
    </>
  );
};

export default HomePage;
