import React from 'react';
import {NavLink} from 'react-router-dom';

const Navigation = () => {
    return ( 
        <nav className="Nav">
            <NavLink className="Nav__link" to="/our-game-ranking/perGame">
                per games
            </NavLink>

            <NavLink className="Nav__link" exact to="/our-game-ranking">
                standard
            </NavLink>

            <NavLink className="Nav__link" to="/our-game-ranking/percentage">
                percentage
            </NavLink>
        </nav>
     );
}

export default Navigation;